<template><div><h1 id="java-线程基础" tabindex="-1"><a class="header-anchor" href="#java-线程基础"><span>Java 线程基础</span></a></h1>
<h2 id="线程概述" tabindex="-1"><a class="header-anchor" href="#线程概述"><span>线程概述</span></a></h2>
<p>线程（Thread）是程序执行的最小单位，一个进程可以包含多个线程。Java 中的线程特点：</p>
<ul>
<li>轻量级</li>
<li>共享进程资源</li>
<li>独立执行</li>
<li>并发执行</li>
</ul>
